.Product_single {
    /* margin: 30px 0 0 0; */
}

.Product_single .inner {
    display: flex;
}

.Product_single .left {
    flex: 1.8;
    padding: 0 50px 0 0;
}

.Product_single .left img {
    width: 100%;
}

.Product_single .right {
    flex: 1.2;
    margin: 10px 20px 0 0;
}

.Product_single .right .dec {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 0 20px 0;
}

.Product_single .right .Kategorie {
    display: flex;
    gap: 10px;
    font-size: 17px;
}

.price {
    font-size: 17px;
    font-weight: 600;
}

.product {
    font-size: 30px;
    font-weight: 600;
    margin: 0 0 10px 0;
}

.sdec {
    margin: 0 0 50px 0;
}

.size {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 0 40px 0;
}

.size>div:nth-child(1) {
    font-size: 15px;
    font-weight: 600;
}

.size>div:nth-child(2) {
    font-size: 13px;
    font-weight: 400;
}

.Product_single .right .color>li {
    display: inline-block;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin: 10px 5px 0 0;
    background: #333;
}

.Product_single .right .color>li:hover {
    border: 2px solid #d41935;
}

.right .sdec {
    display: block;
    font-size: 13px;
    text-align: left;
}

.right .sdec>li {
    display: inline-block;
    padding: 5px 10px;
    border: 1px solid #333;
    border-radius: 15px;
    margin: 0 10px 0 0;
}

.right .sizebox>li {
    display: inline-block;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    margin: 0 15px 15px 0;
    border: 1px solid #333;
}

.pcart {
    display: flex;
    gap: 10px;
    margin: 0 0 30px 0;
}

.detail {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
}

.detail span {
    display: block;
    font-weight: 600;
}

.detail>li {
    font-size: 15px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding: 20px 0;
    line-height: 1.45;
}

.detail>li>a {
    font-size: 15px;
    font-weight: 600;
}

.sbtn {
    display: inline-block;
    width: 100%;
    padding: 20px;
    border-radius: 40px;
    background: #333;
    border: inherit;
    margin: 0 0 10px 0;
}

.count {
    padding: 10px 0;
    margin: 20px 0;
}

.count i {
    display: flex;
    flex-direction: row;
    margin: 0 0 0 100%;

}